import React from 'react'
import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  Row,
  Col,
  Form,
  Button
} from 'reactstrap'
import './login1.css'
import Logo from '../../assets/img/icons/logo11.png'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import Plans from '../Register/Plans'
import '../../assets/scss/pages/authentication.scss'
import * as firebase from 'firebase/app'
import 'firebase/messaging'
import axios from 'axios'
import '../Register/reg.css'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'

class Register extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      password: '',
      mobileNo: '',
      isLoading: false,
      isError: false,
      responseData: {},
      errorMsg: '',
      status: '',
      token: '',
      showPasswordFlag: true,
      isSubmit: false,
      loginText: 'Sign In'
    }
    this._isMounted = false
    this.handleSubmit = this.handleSubmit.bind(this)
    this.showPassword = this.showPassword.bind(this)
    this.hidePassword = this.hidePassword.bind(this)
    this.goToForgotPassword = this.goToForgotPassword.bind(this)
    this.handleChangeUsername = this.handleChangeUsername.bind(this)
    this.handleChangePassword = this.handleChangePassword.bind(this)
    this.subscribe = this.subscribe.bind(this)
  }

  handleChangeUsername(event) {
    this.setState({ mobileNo: event.target.value })
  }

  handleChangePassword(event) {
    this.setState({ password: event.target.value })
  }

  goToForgotPassword() {
    this.props.history.push('/forgotpassword')
  }

  showPassword() {
    this.setState({ showPasswordFlag: true })
  }

  hidePassword() {
    this.setState({ showPasswordFlag: false })
  }

  subscribe(email) {
    console.log('###############################start')
    try {
      axios
        .post('/backendapi/subscribe', {
          token: localStorage.getItem('fcmtoken'),
          topic: email
        })
        .then((res) => {})
    } catch (e) {
      console.log('###############################star4', e)
    }
  }

  handleSubmit(e) {
    this.setState({ errorMsg: '' })
    this.setState({ isSubmit: true, loginText: 'Signing In...' })
    if (this.state.mobileNo === '' || this.state.password === '') {
      this.setState({ errorMsg: 'Invalid Credentials !' })
      // } else if(this.state.mobileNo.length !== 10){
      //   this.setState({ errorMsg: "Username must be of 10 characters only !" });
      // } else if(isNaN(this.state.mobileNo)){
      //   this.setState({ errorMsg: "Username must be numeric only !" });
      // }  else if(!(/^[0-9]+$/.test(this.state.mobileNo))) {
      //   this.setState({ errorMsg: "Username must be numeric only !" })
    } else {
      //console.log()
      //user = {
      // "password": "123",
      // "user": "aaquib123"
      //}
      axios
        .post('/backendapi/user/login', {
          user: this.state.mobileNo.trim(),
          password: this.state.password.trim()
        })
        .then((res) => {
          console.log('Aaquib sss', res)
          if (
            res.data.length > 0 &&
            res.data[0].status !== 'emailNotConfirmed'
          ) {
            const resp = res.data[0]
            this.setState({ errorMsg: '', loggedIn: true })
            console.log('################# login', resp.toString())
            var d = new Date(resp.attendanceDt)
            d.setHours(0, 0, 0, 0)

            var e = new Date()
            e.setHours(0, 0, 0, 0)
            console.log('################# login', d, e)
            let flag = false
            if (d.getTime() === e.getTime()) {
              flag = true
            }
            localStorage.setItem('logInUserData', JSON.stringify(resp))
            localStorage.setItem('logInGreeting', true)
            localStorage.setItem('theme', resp.theme ? resp.theme : 'light')
            this.subscribe(resp.email)
            this.props.history.push('/dashboard')
            window.location.reload()
          } else if (res.data[0].status === 'emailNotConfirmed') {
            console.log('here1')
            const userkey =
              this.props.routerProps &&
              this.props.routerProps.match.params.userkey
            if (res.data[0].status === userkey) {
              const resp = res.data[0]
              this.setState({ errorMsg: '', loggedIn: true })
              console.log('################# login', resp.toString())
              var d = new Date(resp.attendanceDt)
              d.setHours(0, 0, 0, 0)

              var e = new Date()
              e.setHours(0, 0, 0, 0)
              console.log('################# login', d, e)
              let flag = false
              if (d.getTime() === e.getTime()) {
                flag = true
              }
              localStorage.setItem('logInUserData', JSON.stringify(resp))
              localStorage.setItem('logInGreeting', true)
              this.subscribe(resp.email)
              this.props.history.push('/dashboard')
              axios
                .post('/backendapi/update/login', res.data[0])
                .then((res) => {})
              window.location.reload()
            } else {
              this.setState({
                errorMsg: 'Please confirm your email for log in !! ',
                loginText: 'Sign In'
              })
            }
          } else {
            this.setState({
              errorMsg: 'Inavlid Credentials',
              loginText: 'Sign In'
            })
          }
        })
        .catch((error) => {
          if (error.response && error.response.status === 401) {
            this.setState({
              errorMsg: 'Inavlid Credentials',
              loginText: 'Sign In'
            })
          } else {
            console.log('Aaquib')
            this.setState({
              errorMsg: 'Something went Wrong ! Contact Your admin',
              loginText: 'Sign In'
            })
          }
        })
    }
  }

  _handleKeyDownLogin = (event) => {
    if (event.which === 13 || event.keyCode === 13) {
      if (this._isMounted) {
        this.handleSubmit()
      }
    }
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  componentDidMount() {
    document.addEventListener('keydown', this._handleKeyDownLogin)
    this._isMounted = true
  }

  render() {
    if (
      localStorage.getItem('logInUserData') !== '' &&
      localStorage.getItem('logInUserData') !== null
    ) {
      var logInUserData = JSON.parse(localStorage.getItem('logInUserData'))
      if (logInUserData !== null) {
        this.props.history.push('/dashboard')
      }
    }
    let isApiLoading = this.props.isLoading
    let isApiError = this.props.isError
    let apiResponseStatus = this.props.status
    let apiResponseData = this.props.responseData
    let customMsg = ''
    if (!isApiError && apiResponseStatus === 200) {
      customMsg = ''
    } else if (isApiError && apiResponseStatus === 401) {
      customMsg = apiResponseData.fault.businessMessage
    } else if (
      isApiError &&
      ((apiResponseStatus !== 200 && apiResponseStatus !== 401) ||
        apiResponseStatus === '')
    ) {
      customMsg = 'Some error occurred. Please try again later !'
    }
    return (
      <div>
        <div>
          <br />
          <Row className="m-0 justify-content-center">
            <Col
              sm="8"
              xl="7"
              lg="10"
              md="8"
              className="d-flex justify-content-center"
            >
              <Card className="bg-authentication rounded-0 mb-0 w-100">
                <Row
                  className="m-0"
                  style={{
                    padding: '20px'
                  }}
                >
                  <Col lg="6">
                    <div
                      style={{
                        display: 'flex',
                        width: '100%',
                        flexDirection: 'row',
                        background: 'white',
                        height: '80px'
                      }}
                    >
                      <img
                        style={{
                          height: '50px',
                          margin: '5px',
                          marginTop: '20px',
                          paddingBottom: '2px'
                        }}
                        alt="Logo"
                        src={Logo}
                        className="logo12"
                      />{' '}
                      <h2
                        style={{
                          color: '#2c2c2c',
                          fontWeight: '700',
                          marginTop: '28px',
                          textAlign: 'center',
                          width: '70%'
                        }}
                      >
                        Last Arzi
                      </h2>
                    </div>
                    <Plans />
                  </Col>
                  <Col lg="6" md="12" className="p-0">
                    {window.screen.width < 600 && (
                      <div
                        style={{
                          marginTop: '10px',
                          textAlign: 'center',
                          display: 'flex',
                          width: '100%',
                          flexDirection: 'row',
                          background: 'white',
                          borderBottom: '1px solid grey'
                        }}
                      >
                        <img
                          style={{
                            height: '50px',
                            marginTop: '20px',
                            marginLeft: '20px'
                          }}
                          src={Logo}
                          alt="Logo"
                          className="logo12"
                        />{' '}
                        <h2
                          style={{
                            color: '#2c2c2c',
                            fontWeight: '700',
                            marginTop: '28px',
                            width: '70%'
                          }}
                        >
                          {' '}
                          Last Arzi
                        </h2>
                      </div>
                    )}
                    <Card
                      className="rounded-0 mb-0 pt-1"
                      style={{
                        background: '#ffffff'
                      }}
                    >
                      <div className="pb-1 pt-50">
                        <CardTitle
                          style={{
                            background: '#ffffff',
                            width: '100%'
                          }}
                        >
                          <h2
                            className="mb-0"
                            style={{
                              color: '#2c2c2c',
                              fontWeight: '700',
                              marginTop: '7px',
                              textAlign: 'center'
                            }}
                          >
                            Log In
                          </h2>
                        </CardTitle>
                      </div>
                      <CardBody className="pt-1">
                        <div className="form">
                          <h5 style={{ color: 'red' }}>
                            {this.state.errorMsg}
                          </h5>
                          <div>
                            <input
                              style={{
                                textAlign: 'center',
                                border: '1px solid grey',
                                borderRadius: '5.5px'
                              }}
                              type="text"
                              name="user"
                              placeholder="Username"
                              onChange={this.handleChangeUsername}
                            />
                            <input
                              style={{
                                textAlign: 'center',
                                border: '1px solid grey',
                                borderRadius: '5.5px'
                              }}
                              type="password"
                              name="password"
                              placeholder="Password"
                              onChange={this.handleChangePassword}
                            />
                            <br />
                            <br />

                            <button
                              onClick={this.handleSubmit}
                              style={{
                                border: '1px solid grey',
                                borderRadius: '5.5px'
                              }}
                            >
                              {this.state.loginText}
                            </button>
                            <br />
                            <br />
                            <br />
                            <div
                              onClick={() => {
                                this.props.history.push('/register')
                              }}
                              style={{ color: '#4CAF50' }}
                            >
                              <div>Need a new account ?</div>
                              <u>Please Register</u>
                            </div>
                          </div>
                        </div>

                        {/* <div>
                          <Form>
                            <div
                              className="d-flex justify-content-between"
                              style={{ float: "right", marginRight: 0 }}
                            >
                              <Button.Ripple color="primary" type="submit">
                                Next
                              </Button.Ripple>
                            </div>
                            <br />
                            <br />
                            <br />

                            <div
                              onClick={() => {
                                this.props.history.push("/register");
                              }}
                              style={{ float: "right", color: "#4CAF50" }}
                            >
                              <span>Don't you have account yet?</span>
                              <u>Register</u>
                            </div>
                          </Form>
                        </div>
                       */}
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </Card>
            </Col>

            {window.screen.width < 600 && (
              <Col lg="6" md="12" className="p-0">
                <Card className="bg-authentication rounded-0 mb-0 w-100">
                  <CardBody className="pt-1 pb-50">
                    <div style={{ width: '300px' }}>
                      <Plans />
                    </div>
                  </CardBody>
                </Card>
              </Col>
            )}
          </Row>
        </div>
      </div>
    )
  }
}
export default Register
